.itemContainer {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  width: 100%;
  border: 1px solid #800080;
  border-radius: 20px;
  height: 100%;

  .itemImage {
    max-height: 250px;
    object-fit: cover;
    border-radius: 20px 20px 0 0;
  }

  .itemInfo {
    font-family: Arial Helvetica sans-serif;
    height: 100%;
    padding: 1rem;
  }

  .addToCartButton {
    border-radius: 0 0 20px 20px;
  }
}
